import { createHashRouter, Navigate } from 'react-router-dom'
import { lazy, Suspense } from 'react'
import NotFound from '@/views/NotFound'
import LoadingStatus from '@/components/LoadingStatus/LoadingStatus'
//
const Login = lazy(() => import('@/views/Login/Login'))
// 
const LayoutBox = lazy(() => import('@/views/Layout/LayoutBox'))
// 
const PageIndex = lazy(() => import('@/views/PageIndex/PageIndex'))
//
const ButtonPage = lazy(() => import('@/views/ButtonPage/ButtonPage'))
//
const IconPage = lazy(() => import('@/views/IconPage/IconPage'))
// 
const About = lazy(() => import('@/views/About/About'))
//
const FormBaseView = lazy(() => import('@/views/FormView/FormBaseView/FormBaseView'))


//
const router = createHashRouter([
   {
      path: '/login',
      element: <Suspense fallback={<LoadingStatus />}><Login /></Suspense>
   },
   {
      path: '/',
      element: <Suspense fallback={<LoadingStatus />}><LayoutBox /></Suspense>,
      children: [
         {
            index: true,
            element: <Navigate to="/index"></Navigate>
         },
         {

            path: 'index',
            element: <Suspense fallback={<LoadingStatus />}><PageIndex /></Suspense>,
         },
         {

            path: '/public/button',
            element: <Suspense fallback={<LoadingStatus />}><ButtonPage /></Suspense>,
         },
         {

            path: '/public/icon',
            element: <Suspense fallback={<LoadingStatus />}><IconPage /></Suspense>,
         },
         {
            path: 'about',
            element: <Suspense fallback={<LoadingStatus />}><About /></Suspense>,

         },
         {
            path: '/form/base-form',
            element: <Suspense fallback={<LoadingStatus />}><FormBaseView /></Suspense>,
         }
      ]
   },

   {
      path: '*',
      element: <NotFound />
   }
])

export default router